import React from 'react';
import InstitutionHeader from '../components/layout/InstitutionHeader';
import InstitutionFooter from '../components/layout/InstitutionFooter';
import InstitutionalHero from '../components/sections/InstitutionalHero';
import InstitutionalFeatures from '../components/sections/InstitutionalFeatures';
import InstitutionTypes from '../components/sections/InstitutionTypes';

export default function HomePage() {
  return (
    <div className="min-h-screen bg-slate-50">
      <InstitutionHeader />
      
      {/* Hero Section */}
      <InstitutionalHero />
      
      {/* Institution Types Section */}
      <InstitutionTypes />
      
      {/* Features Section */}
      <InstitutionalFeatures />
      
      <InstitutionFooter />
    </div>
  );
}
